.contact {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(3, 1fr);
  border-bottom: solid 2px #ccc;
  -ms-flex-align: center;
      align-items: center;
  font-family: "Niramit", Helvetica, sans-serif; }
  .contact > div {
    margin: 0.5rem;
    padding-left: 0.25rem;
    border: solid 1px #1d3900; }
    .contact > div > input {
      background-color: #9fc177;
      border: none; }
  .contact .deleteContact {
    width: 15%;
    justify-self: end;
    grid-column: 2/3;
    grid-row: 1/2;
    background-color: #9fc177;
    border: none;
    cursor: pointer; }
    .contact .deleteContact :hover {
      border: solid 1px #ccc; }
  .contact .name {
    grid-column: 1/2;
    grid-row: 1/2; }
  .contact .phoneNumber {
    grid-row: 2/3; }
  .contact .email {
    grid-row: 3/4; }

@media (max-width: 460px) {
  .contact {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, 1fr); }
    .contact .deleteContact {
      justify-self: baseline;
      grid-column: 1/2;
      grid-row: 4/5; } }



