.calendar {
  width: 90%;
  height: 90%;
  justify-self: center;
  align-self: center;
  display: grid;
  grid-template-rows: 1fr 5fr;
  background-color: #9fc177;
  border: solid 3px #c1ceb5;
  border-radius: 1.5%; }

@media (max-width: 460px) {
  .calendar {
    grid-template-rows: 1fr 2fr; } }



