.weather {
  width: 90%;
  height: 70%;
  grid-column: 1/2;
  grid-row: 2/3;
  justify-self: center;
  -ms-flex-item-align: center;
      align-self: center;
  border: solid 3px #c1ceb5;
  border-radius: 6px;
  background-color: rgba(167, 199, 136, 0.747); }

@media (max-width: 460px) {
  .weather {
    grid-column: 1/-1; } }



